<template>
  <div class="container" v-if="isReady()">
    <div class="product-details">
      <h1>{{ name }}</h1>

      <p class="information">
        {{ instructions }}
      </p>
    </div>

    <div class="product-image">
      <img :src="imageLink" alt="" />
      <div class="info">
        <h2>Ingredients</h2>
        <h1 class="drinkId">{{ drinkId }}</h1>

        <ul v-for="(each, i) in ingredients" :key="i">
          <li :key="i">
            <strong>{{ each.ing }}</strong> - {{ each.mes }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "card",
  methods: {
    isReady() {
      return typeof this.type === "string";
    },
  },
  props: {
    type: {
      type: String,
      default: "",
    },
    category: {
      type: String,
      default: "",
    },
    name: {
      type: String,
      default: null,
    },
    alternative: {
      type: String,
      default: "",
    },
    imageLink: {
      type: String,
      default: "",
    },
    glass: {
      type: String,
      default: "",
    },
    ingredients: {
      type: String,
      default: "",
    },
    instructions: {
      type: String,
      default: "",
    },
    drinkId: {
      type: String,
      default: null,
    },
  },
};
</script>

